/* App.css - Global Styles */
body {
    font-family: 'Arial', sans-serif;
    background-color: #1e1e1e;
    color: white;
    margin: 0;
    padding: 0;
}

.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px;
    background: #282c34;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.5);
}

.nav-links a {
    color: white;
    text-decoration: none;
    margin: 0 15px;
    font-size: 16px;
}

.auth-buttons button {
    background: #007bff;
    color: white;
    border: none;
    padding: 8px 15px;
    cursor: pointer;
    border-radius: 5px;
}

.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    text-align: center;
}

input, textarea {
    width: 300px;
    padding: 10px;
    margin: 10px 0;
    border-radius: 5px;
    border: 1px solid #ccc;
}

button {
    background: #007bff;
    color: white;
    padding: 10px 20px;
    border: none;
    cursor: pointer;
    border-radius: 5px;
    transition: 0.3s;
}

button:hover {
    background: #0056b3;
}

/* Admin.css - Admin Specific Styles */
.admin-container {
    width: 80%;
    margin: auto;
    background: #2c2f33;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0px 4px 10px rgba(255, 255, 255, 0.1);
}

.admin-table {
    width: 100%;
    border-collapse: collapse;
    background: #3a3f44;
    color: white;
}

.admin-table th, .admin-table td {
    padding: 10px;
    border: 1px solid #ddd;
}

.admin-table th {
    background: #4b5158;
}

footer {
    text-align: center;
    padding: 20px;
    background: #282c34;
    position: fixed;
    width: 100%;
    bottom: 0;
    color: white;
}
